import { Component, OnInit } from '@angular/core';
import { ReviewsService } from '../../../../../auth/_services/reviews.service';
import { Router, ActivatedRoute } from "@angular/router";
import { ToastrService } from 'ngx-toastr';


@Component({
    selector: 'app-reviews-list',
    templateUrl: "reviews-list.component.html",
    styles: []
})
export class ReviewsListComponent implements OnInit {


    constructor(private toastr: ToastrService, private router: Router, private _reviewsService: ReviewsService) { }
    public reviewsData;
    public searchValue = "";
    public dropDownValue = '';
    p: number = 1;
    ngOnInit() {

        /*user permission*/
        let currentUser = JSON.parse(localStorage.getItem('currentUser'));
        let permission = currentUser.success.permission;
        if (permission.reviews_list != 1) {
            this.toastr.error('You have not permission to visit this page.', 'Error Message');
            this.router.navigate(['/']);
        }
        /*user permission*/

        // localStorage.setItem('section', 'customer');

        this._reviewsService.getAll().subscribe(
            data => {
                console.log('data');
                console.log(data);
                this.reviewsData = data.success;
            }
        );

        // console.log("hello");
    }

    public search() {
        this._reviewsService.searching({ search_value: this.searchValue, dropdown_value: this.dropDownValue }).subscribe(
            data => {
                console.log(data);
                this.reviewsData = data.success;
            }
        );
    }

    public reviews_delete(del_id) {

        console.log(del_id);

        let value = confirm('Are you want to delete this reviews ?');
        if (value) {

            this._reviewsService.del_reviews(del_id).subscribe(
                data => {
                    this.reviewsData = data.data;
                }
            );
        }

    }


    public changeStatus(reviews: any) {

        if (reviews.reviews_status == 'Active') {
            reviews.reviews_status = 'Inactive';

            this._reviewsService.updateStatus(reviews.id).subscribe(
                data => {
                    console.log('update', data);
                }
            );
        } else {
            reviews.reviews_status = 'Active';
            this._reviewsService.updateStatus(reviews.id).subscribe(
                data => {
                    console.log('update', data);
                }
            );
        }
    }


}
